import React, { useState, useEffect, useRef } from 'react'
import { ChatListElement } from './ChatListElement'
import { State } from '../../redux/store'

export function ChatList({state, selectChat, downloadFile}:{state: State, selectChat: (id: number) => void, downloadFile: Function}) {
	const [dragging, setDragging] = useState(false)
	const [position, setPosition] = useState({left: 0, top: 0})
	const [lastPosition, setLastPosition] = useState({left: 0, top: 0})

	const chatListScrollBar = useRef(null)
	const chatListScrollPane = useRef(null)

	useEffect(() => {

		const onMouseMove = (e: any) => {
			setPosition({
				left: position.left + e.pageX - lastPosition.left,
				top: Math.max(0, position.top + e.pageY - lastPosition.top)
			})
			setLastPosition({left: e.pageX, top: e.pageY})
		}

		const onMouseUp = () => {
			setDragging(false)
		}

		if (dragging) {
			document.addEventListener('mousemove', onMouseMove)
			document.addEventListener('mouseup', onMouseUp)
		}

		return () => {
			document.removeEventListener('mousemove', onMouseMove)
			document.removeEventListener('mouseup', onMouseUp)
		}
	}, [dragging])

	const onMouseDown = (e: any) => {
		setLastPosition({left: e.pageX, top: e.pageY})
		setDragging(true)
	}

	const sliderMaxY = chatListScrollBar.current != null? (chatListScrollBar.current as any).offsetHeight : 0
	const sliderHeight = 100 // TODO
	const sliderY = Math.min(Math.max(position.top, 0), sliderMaxY - sliderHeight)
	const progress = Math.min(sliderY / (sliderMaxY - sliderHeight), 1.0)
	const paneH = chatListScrollPane.current != null? (chatListScrollPane.current as any).offsetHeight : 0
	const paneY = -Math.round(progress * (paneH - sliderMaxY))
	//window.document.title = '' + progress + '%'
	// TODO use "progress%" state instead

	// TODO
	const onMouseClick = (e: unknown) => {
		//setPosition({
		//	left: position.left,
		//	top: e.offsetY
		//})
	}

	const onWheel = (e: any) => {
		//progress = Math.min(sliderY / (sliderMaxY - sliderHeight), 1.0)
		//progress = Math.min(sliderY / (sliderMaxY - sliderHeight), 1.0)
		setPosition({...position, top: Math.min(Math.max(0, position.top + e.deltaY * 0.5), sliderMaxY - sliderHeight)})
	}

	const sortedChats: number[] = [...state.chatIds].sort((a: number, b: number): number => {
		const ordera: BigInt = BigInt(state.chats[a].order)
		const orderb: BigInt = BigInt(state.chats[b].order)
		//return Math.random() - 0.5
		if (ordera > orderb) return -1
		if (ordera < orderb) return +1
		return 0
	})

	return <div className="chats" onWheel={onWheel}>
		<div className="chatListScrollPane" ref={chatListScrollPane} style={{top: paneY + 'px'}}>
		{
		sortedChats.map(chatId => <ChatListElement downloadFile={downloadFile} key={chatId} chatId={chatId} state={state} selectChat={selectChat}/>)
		}
		</div>
		<div className="chatListScrollBar" onMouseDown={onMouseClick} ref={chatListScrollBar}></div>
		<div className="chatListScrollBarSlider" onMouseDown={onMouseDown} style={{top: sliderY + 3+ 'px'}}></div>
	</div>

//	const demo = [
//		{ name: 'Saved Messages', date: 'Tue', text: 'Hellooooooooooooooooooooo', unread: 12000, current: true },
//		{ name: 'Saved Messages', date: 'Tue', text: 'Hellooooooooooooooooooooo', unread: 0, who: 'expwez' },
//		{ name: 'Hello Kitty', date: '1:43 AM', text: 'Heil miledy', unread: 95, who: 'expwez' },
//		{ name: 'Hello Kitty', date: '1:43 AM', text: 'Heil miledy y y y y y y y y y', unread: 95, who: 'expwez' },
//		{ name: 'TON isAFail!!!!!!!!!!!!!!', date: '11.04.20', text: 'Ha-ha true' },
//	]
//
//	let key = 0
//	return <div className="chats">{demo.map(props => <ChatElement key={key++} {...props}/>)}</div>
}
